import React from 'react'
import chovena from '../assets/img/Chovena.jpg'
import chovena2 from '../assets/img/Chovena (2).jpg'

export const Pando = () => {
  return (
    <div className="container">
      <div className="column-leftpando">
        {/* Contenido de la columna izquierda */}
        <h2>Chovena</h2>
        <h4>Origen</h4>
        <p>
        De origen chiquitano, la chovena como ritmo y danza autctona ha logrado dispersarse en las regiones chiquitanas, guarayas y moxeas del oriente boliviano.
        Esta danza es de ritmo marcadamente autctono y de movimientos alegres, frases cortas, constantemente repetidas en comps de 2/4.
        Tiene bastantes analogas con el taquirari: sistemas rtmicos y tonales, binarios, sincopados etc. No radamente pentatnicas, que hacen pensar no en un lejano intercambio con la msica de la montaa. Desde la colonia el mestizaje es evidente, sobre todo con la aparicin del ritmo ternario por lo que ser un anacronismo atriburselo a la chovena originaria.
        Esta es una de las danzas ms antiguas, tanto de la parte espaola como de los jesuitas en el proceso de evangelizacin. Los misioneros lo rescataron y lo preservaron como parte de la musicalizacin, inclusive la sacra. No tena letra, y le incluyeron violines para lograr una mayor cadencia y sonoridad.
        </p>
        <h4>Descripcin de la danza</h4>
        <p>
        La chovena se inicia con el ritmo que imponen las flautas, violines, la guitarra y las tamboras. En dos filas, una de mujeres y la otra de hombres, los danzarines inician su recorrido con un semitrote. Las mujeres se toman la cintura. Los hombres levantan las manos dando palmadas que acompaan el ritmo de las tamborillas. Luego los hombres se adelantan a las mujeres, levantando sus sombreros con las dos manos hasta donde alcanzan sus brazos, luego los bajan en seal de reverencia. Luego giran, se colocan el sombrero y vuelven a su posicin inicial. Entonces las mujeres avanzan, siempre tomndose con ambas manos la cintura y con ese paso rpido avanzan zarandeando sus cinturas y sonriendo. Pronto, levantan los brazos y empiezan a aplaudir, en ese momento la fila de hombres avanza hasta alcanzarlas y colocarse a su lado. Despus, tomndose de las manos de sus respectivas parejas, hombres y mujeres levantan sus manos y con un movimiento rpido forman un crculo, siempre sostenindose de las manos. Seguidamente se juntan y separan cual si fuesen abanicos, girando primero de izquierda a derecha y, despus, en sentido contrario. Finalmente, se sueltan de las manos y forman parejas, para avanzar otro trecho tomados de la cintura, nuevamente se sueltan y levantan las manos para repetir ms all la coreografa, siempre a ritmo de semitrote.
        </p>
      </div>
      <div className="column-rightpando">
        {/* Contenido de la columna derecha */}
        
        <img src={chovena} alt='logo' className="caporales2-img"></img>
        <br></br>
        <br></br>
        <br></br>
        <img src={chovena2} alt='logo' className="caporales2-img"></img>
    
      </div>
</div>
  )
}
