import React from 'react'
import sarao from '../assets/img/sarao.jpg'
import sarao2 from '../assets/img/sarao2.jpg'

export const SantaCruz = () => {
  return (
    <div className="container">
      <div className="column-leftsantacruz">
        {/* Contenido de la columna izquierda */}
        <h2>Sarao</h2>
        <h4>Origen</h4>
        <p>
        La danza Sarao (baile de las cintas, trenzado) es del oriente boliviano es una danza tradicional en varias culturas. Generalmente, se baila en el carnaval o como un smbolo de unin entre los pueblos.
        El Sarao consiste en que un grupo de bailarines o bailarinas, pares y generalmente ms de ocho, va trenzando cintas, entrecruzndose acompasadamente y en forma alternada al contorno de un mstil central, en el que va quedando un trenzado (por eso tambin es llamado danza trenzado). Para los bailarines el palo significaba la llegada de la primavera, para vivificar los surcos, los frutales, el ganado y las mujeres. Por eso, lo adornaban con cintas de colores (smbolos de frutos) y danzaban en torno a l.
        La danza Sarao tiene en cada regin variantes en su interpretacin, sobre todo en las figuras del trenzado que realizan con las cintas que representan el tejido tpico que ellos mismos hacen para confeccionar sus vestimentas.
        </p>
        <h4>Danza</h4>
        <p>
        Esta danza Sarao est constituida por 12 personas 6 mujeres, 6 varones. Ingresan en dos filas bailando, cada uno con su cinta direccin convergente; el portaestandarte lleva un mstil caminando por delante hasta un sitio elegido, planta el mstil. Se colocan los danzantes en circulo, rodeando el estandarte de cintas, vista al centro y sujetando cintas con la mano derecha por el extremo inferior, y cada uno alternando hombres y mujeres comienzan a dar vueltas en su sitio y luego inician el avance alrededor del crculo trenzando en el palo, haciendo de esta manera un combinado de colores, luego comienzan a destrenzar para repetir la marcha con lo que finaliza la danza con una reverencia en smbolo de adoracin.
        </p>
        <h4>Vestimenta</h4>
        <p>
        Mujeres
        Llevan telas vistosas, adornadas con blondas blancas, preparadas en tipoyes.
        Trenzas largas, adornados con cinta de color,  cuelgan por los hombros hasta el pecho o en la espalda.
        En el cuello llevan collares, adornados de plata o semillas silvestres.
        Sombrero de paja adornada con flores.
        Varones
        Visten pantaln blanco.
        Camisa blanca manga larga sin arremangar.
        Pauelo rojo amarrado al cuello.
        Sombrero de paja y abarcas.
        </p>
      </div>
      <div className="column-rightsantacruz">
        {/* Contenido de la columna derecha */}
        
        <img src={sarao} alt='logo' className="caporales2-img"></img>
        <br></br>
        <br></br>
        <br></br>
        <img src={sarao2} alt='logo' className="caporales2-img"></img>
    
      </div>
</div>
  )
}
