import React from 'react'
import tinku from '../assets/img/Tinku.jpg'
import tinku2 from '../assets/img/Tinku (2).jpg'
import potolo from '../assets/img/Potolos 17.jpg'

export const Potosi = () => {
  return (
    <div className="container">
       <div className="column-leftpotosi">
        {/* Contenido de la columna izquierda */}
        <h2>Tinku</h2>
        <h4>Origen </h4>
        <p>
        La danza Tinkus actual se han convertido en una diversin popular, en ella las peleas se dan simplemente como una expresin artstica, como ocurre en cualquier disciplina deportiva de combate, aunque hasta hoy todava se aprecian enemistades entre comunidades y familias.
        En los ltimos aos tambin la danza Tinkus ha sufrido una tergiversacin al ser presentada como una danza folclrica, que rpidamente se hizo muy popular, especialmente en las ciudades y que cada vez se aleja ms del origen y significado propio.
        Colorido con movimientos fuertes, bailado en grupos separados de hombres y mujeres acompaados de msica contagiosa. Los grupos ms importantes de bailarines de Tinku participan en el Carnaval de Oruro.
        </p>
        <h4>Ritual Ceremonial del Tinku</h4>
        <p>
        Practicado anualmente el mes de mayo en la Fiesta de la Cruz, en el pueblo de Macha perteneciente a la provincia de Chayanta en el departamento de Potos. En la Fiesta de la Cruz se juntan 62 comunidades que se medirn a golpes mediante combates de 2, donde la sangre cae en abundancia. La sangre representa un sacrificio u ofrenda para la Madre Tierra o Pachamama, para asegurar que la cosecha de ese ao sea abundante.
        </p>
        <img src={tinku} alt='logo' className="caporales-img"></img>
        <h4>Vestimenta</h4>
        <p>
        La ropa tradicional del Tinku viene a ser muy colorida y atractiva en los pueblos para la ceremonia.
        La ropa de los hombres la confeccionaban las mujeres y la ropa de las mujeres la confeccionaban los hombres. El conjunto de vestimenta de la mujer as como del hombre tiene varios accesorios.
        Tinku mujer
        El vestido de bayeta o Aqsu: con mangas anchas y abertura en la parte posterior al cuello.
        Awayo: tradicionalmente utilizado para transportar productos sobre la espalda, envolver y cargar al bebe o wawa.
        Tullmas: adornos que utilizan para entrelazar las largas trenzas a manera de no atravesarse mientras realizan las labores del da.
        Almilla: vestido largo con el borde adornado de atractivos bordados, una manga corta hasta el antebrazo que deja ver la camisa. Se elabora tradicionalmente de bayeta negra.
        Rebozo: que viene a ser una manta con bordados coloridos y brillantes que se caracterizan por tener la forma de flores.
        El sombrero: que esta fabricado de lana de oveja. En la tradicin las mujeres solteras adornan los sombreros con plumas blancas y rosadas acompaadas de espejos pequeos.
        Las Abarcas rsticas: ojotas o sandalias que utilizan en los pies tanto las mujeres como los hombres.
        Tinku varn
        Lleva la tradicional montera Tinku: que tiene la forma similar a los cascos utilizados por los conquistadores espaoles, fabricado con cuero de res crudo. La parte superior de la montera est adornado con plumas de and, en el encuentro de Tinku es utilizada para proteger la cabeza de los golpes del contrincante.
        LLuchu: es un gorro tejido de lana y es utilizado nicamente por el varn. Es una prenda utilizada a diario pero son estrenados o utilizados por primera ves durante las fiestas. El modo de tejido del gorro identifica al lugar originario de los varones.
        Chuspa: es una bolsa tejida utilizada para llevar la coca en las ceremonias del akulliku, en otro caso es utiliza para cosas pequeas incluso para transportar dinero.
        Tipa, Chupa o Waylla Wallya: es una chalina o bufanda que solo cumple la funciones de adorno festivo, doblada en dos y fijada en la parte posterior de la cintura.
        El Chumpi o faja: es utilizada al rededor del pantaln, a manera de cinturn, tambin es utilizado por las mujeres para sujetar la pollera al nivel de la cintura.
        La Calzona: pantaln hecho de bayeta de color blanco o negro, esta prenda no tiene bolsillo o abertura al medio.
        El Chaleco: prenda utilizada por los varones de bayeta, estos van adornados de bordados con colores vivos de lana o hilo.
        Las Sicas o Polainas: son prendas tejidas de lana multicolores formando rombos en todo lo largo, el tamao va desde el tobillo hasta la rodilla.
        ucku: es una especie de manopla fabricado con cuero de res.
        </p>
        <img src={tinku2} alt='logo' className="caporales-img"></img>


        <br></br>
        <br></br>

      
      </div>
      <div className="column-rightpotosi">
        {/* Contenido de la columna derecha */}

        <h2>Potolos</h2>
        <h4>Origen</h4>
        <p>
        No se sabe con exactitud el significado del origen de la palabra Potolo, segn versiones de los pobladores el nombre se dio debido a la forma de vestir de los varones ya que sus camisas y pantalones eran de bayeta utilizando tambin el aguayo donde llevaban su merienda, su coca y la famosa leja, adems del sombrero y las abarcas.
        </p>
        <h4>Caractersticas de la danza Los Potolos</h4>
        <p>
        Lo llamativo de la danza los Potolos es el movimiento corporal es el ritmo de las caderas, protagonizado por los Macjtas y Mamas, que en su rutina diaria del trabajo en el agro, rinden homenaje a la abundancia de los productos, recojo de los mismos, se desplazan con pasos giles sincronizados en parejas o en grupos, con avances hacia delante y viceversa.
        Si algo en particular distingue la vestimenta de estos grupos es el sombrero confeccionado con lana de oveja de forma semicircular y un ala angosta, descarta cualquier influencia de sombreros espaoles.
        La danza los Potolos expresa la tristeza pero su baile pregona la supervivencia de su clase, el consuelo de su tristeza, como algo nacido de su suelo y de su propio ser. La danza es atrevida e insinuante, graciosa y provocativa por el movimiento de sus caderas y el giro simultneo de la cabeza demostrando una coquetera y cortejo entre las parejas.
        La festividad ms recordada y celebrada para los Potolos es en honor a la Virgen de Santa Rosa de Lima que se la celebra cada tercer domingo de abril tambin participan en los carnavales, ofreciendo el agradecimiento a la Diosa de la Tierra denominada Pachamama por la fertilidad de sus tierras.
        </p>
        <h4>Vestimenta</h4>
        <p>
        Varones
        Una camisa
        Pantaln de bayeta color blanco
        Un sombrero pequeo
        Una faja
        Un aguayo
        Abarcas
        Mujeres
        Un vestido con los colores de la regin
        Un sombrero
        Una lijlla
        Chumpi
        Abarcas
        La meloda de la msica marca un comps lento y alegre.
        El valor cultural de esta danza es realmente inmenso, marcando una diferencia notable con respecto a las dems danzas de nuestro pas.
        </p>
        <img src={potolo} alt='logo' className="caporales2-img"></img>
        <br></br>
        <br></br>

        
     </div>        
</div>
  )
}
