import React from 'react'
import pujllay from '../assets/img/pujllay01.jpg'
import pujllay2 from '../assets/img/Pujllay.jpg'
import bailecito from '../assets/img/bailecito.jpg'
import bailecito2 from '../assets/img/Bailecicto.jpg'
import cuecachuquisaquea from '../assets/img/cueca chuquisaquea.jpg'
import cuecachuquisaquea2 from '../assets/img/Cueca Chuquisaquea (2).jpg'
import huayo from '../assets/img/Huayo.jpg'
import huayo2 from '../assets/img/huayo (2).jpg'

export const Chuquisaca = () => {
  return (
    <div className="container">
      <div className="column-leftchuquisaca">
        {/* Contenido de la columna izquierda */}
        <h2>Pujllay</h2>
        <h4>Origen </h4>
        <p>
        Se sostiene que el Pujllay surge como tributo a la festividad del Jatun Pocoy (gran madurez) y el Pauker Waray (sacrificio al sol), en Tarabuco y sus alrededores. Es un ritual pagano como gratitud a la Pachamama solicitando que haya una buena cosecha.
        Mas adelante se sum como motivo de esta festividad la victoria de los Yamparez sobre los espaoles el 12 de marzo de 1816 en la batalla de Jumbate.
        </p>
        <h4>Caractersticas de la danza Pujllay</h4>
        <p>
        Los danzarines visten una montera en la cabeza adornada con flores rosadas, blancas y amarillas. Luego est una almilla, parecida a una blusa de pao negro y otra de pao blanco, desde las piernas a las canillas. Tambin estn las polainas que cubren las piernas, desde el taln hasta las canillas con colores llamativos.
        Luego est el Uncku Phallado (poncho) que se coloca en el cuello y luce figuras tejidas. Tambin estn las chuspas que son tejidas de lana de oveja. En cada una de las manos llevan dos pauelos de seda fina.
        Otros elementos importantes de la danza Pujllay son el cincho; pathu ojotas que tienen plantas altas con espuelas bastante ruidosas.
        En cuanto a la coreografa se refiere, ste es de un ritmo lento por lo que ms destaca es la coreografa.
        </p>
        <img src={pujllay} alt='logo' className="caporales2-img"></img>
        <h4>Vestimenta</h4>
        <p>
        Las mujeres visten pacha montera (sombrero con dos puntas y mucha decoracin), aymilla (vestido), aqsu (falda tejida con adornos de animales y eventos de la vida cotidiana), lliklla (aguayo sostenido por un prendedor de plata). Los hombres usan montera (hecha de cuero negro), kunkaunku (pequeo poncho), aymilla (camisa), sinchu (cinturn ancho de cuero curtido de donde cuelgan campanillas que dan ritmo a la msica), calzuna (pantaln negro con volantes blancos), sunri (polainas) y las llamativas ujutas (sandalias de suela muy alta con espuelas).
        </p>
        <img src={pujllay2} alt='logo' className="caporales2-img"></img>
        <br></br>
        <br></br>

        <h2>Bailecito</h2>
        <h4>Origen </h4>
        <p>
        La danza Bailecito es de galanteo de parejas sueltas e independientes, de movimientos vivos. La danza tiene origen europeo pero se baila tambin en Bolivia. La danza es de influencia incaica, se lo baila con pauelo y hay bailecitos en diferentes regiones del pas.
        La forma tradicional del Bailecito Chuquisaqueo es sincopado y se repite tres veces. Se desarrolla en comps ternario, cuarta aumentada, paralelo de terceras y acompaado de armona y punteo de charangos, guitarras y arpas con repiqueteo de bombos.
        Es un baile de parejas independientes quienes bailan con movimientos simultneos, cadenciosos, pauelo a la mano derecha, con pasos llenos de coquetera y donaire reflejando as el juego amoroso que los une. Las letras de la danza Bailecito toman diversas situaciones de la vida como la nostalgia, la pena, el reclamo amoroso, las risas, el anhelo personal y dems que estn hbilmente estructuradas por inspirados letristas y compositores.
        </p>
        <img src={bailecito} alt='logo' className="caporales2-img"></img>
        <h4>Vestimenta</h4>
        <p>
        Mujer: peinado a dos bandas con una o  dos trenzas adornado con flores. Aros de argolla, blusa de algodn en colores lisos o floreados y faldn amplio, escote mediando con mangas sueltas hasta los antebrazos, adornos de volado en el escote y terminacin de las mangas. Falda amplia no necesariamente del color de la blusa, hasta los empeines con volado en el ruedo. Tambin puede usar vestido con talle a la cintura e iguales caractersticas para ele escote, mangas y falda. Enagua y calzones blancos. Zapatos de taco bajos, botas o alpargatas placas con bordados.
        Varn: chambergo de copa chata o copa redonda y alta con alas cortas. Camisa blanca con o sin cuello. Pauelo liso colocado en forma triangular sobre la espalda. Chaleco oscuro con o sin bordados. Faja y tirador con rastra. Calzoncillo blanco de puo adentro de la bota. Chiripa de merino negro, bota fuerte. Espuelas, poncho. Facn.
        </p>
        <img src={bailecito2} alt='logo' className="caporales2-img"></img>
   
      </div>

      <div className="column-rightchuquisaca">
        {/* Contenido de la columna derecha */}
        <h2>Cueca Chuquisaquea</h2>
        <h4>Origen </h4>
        <p>
        La danza cueca chuquisaquea es de carcter romntico; la disposicin en filas entre damas y caballeros, el rigor de los tiempos, la mirada altiva, el atuendo de rigor y taco alto, la pollera plisada debajo de las rodillas, la blusa bordada adornada de volados, la mantilla de seda y el pauelo blanco, son algunos de los detalles que componen la expresin ms antigua y mejor preservada hasta nuestros das de las danzas coloniales de saln de la vieja ciudad de La Plata, actual ciudad de Sucre.
        </p>
        <img src={cuecachuquisaquea} alt='logo' className="caporales2-img"></img>
        <h4>Vestimenta</h4>
        <p>
        El vestuario segn el tipo de cueca representada; es decir, la mujer viste de Chola chuquisaquea o potosna usando una pollera relativamente larga y botas, mientras que en Cochabamba y Tarija las polleras son ms cortas y la chaquea utiliza un falda larga floreada.
        </p>
        <img src={cuecachuquisaquea2} alt='logo' className="caporales2-img"></img>

        <h4>Tipos de Cueca</h4>
        <p>
        Este baile popular en Bolivia tiene variaciones en distintos departamentos:
        La cueca chuquisaquea: de un carcter valseado, la disposicin en filas entre damas y caballeros, el rigor de los tiempos, la mirada altiva, el atuendo de rigor y taco alto, la pollera plisada debajo de las rodillas, la blusa bordada adornada de volados, la mantilla de seda y el pauelo blanco, son algunos de los detalles que componen la expresin ms antigua y mejor preservada hasta nuestros das de las danzas coloniales de saln de la vieja ciudad de La Plata, actual ciudad de Sucre.
        La cueca potosina: que combina partes tristes con alegres, y es bailada muy elegantemente, aunque con atuendos distintos de la chuquisaquea, por ejemplo la pollera plisada es ms larga y no usa el taco alto.
        La cueca pacea.
        La cueca orurea: estas son similares en su estructura: ambas tienen modos menores melanclicos, son bailadas en fiestas, chicheras y acontecimientos como presteros y otros.
        La cueca cochabambina: de caractersticas ms populares y sencillas.
        La cueca tarijea.
        La cueca chaquea: estas son ms saltadas y coquetas, en especial la chaquea, que se caracteriza por su gallarda.
        </p>
        

        <br></br>
        <br></br>
        <h2>Huayo</h2>
        <h4>Origen </h4>
        <p>
        La danza El Huayo (tambin llamdo Huaio o Wayo), de origen incaico, es una de las danzas ms representativas de los Andes pues combina elementos pre-hispnicos de los pueblos Quechua y Aymara con los de la influencia europea. Actualmente, es un baile enteramente festivo; pero muchos histricos especulan que en sus orgenes puede haber sido una danza incaica funeraria.
        El huayo puede ser de naturaleza triste y melanclica o por otro lado, festiva y alegre. Se caracteriza por ser bailado en grupos de varias personas, quienes tomadas de las manos trazan diferentes contorciones y caprichosas figuras al ritmo de la msica.
        </p>
        <img src={huayo2} alt='logo' className="caporales2-img"></img>
        <h4>Vestimenta</h4>
        <p>
        Siendo una danza de los nativos indgenas el danzante varonil utiliza un poncho de la regin local. En el Altiplano viene siendo un poncho color caf o tierra y con un sombrero blanco. En los valles se usa colores ms llamativos y en lugares ms calurosos ya no se usa ponchos, ms bien camisa y pantaln blanco. En tiempos modernos para la danza El Huayo no hay una vestimenta reglamentaria para este baile. Se lo ve bailado por mujeres de pollera tal como se lo ve bailado por mujeres de falda. En las fiestas de casa es costumbre que el Huayo viene inmediatamente despus de la Cueca. En eventos grandes como La Fiesta de Urkupia, fraternidades como Los Inti Huayras, Los Antawaras, y otros se visten de ponchos brillosos con decorados de lentejuelas. Las muchachas se visten de mini-polleras, blusas tradicionales, y cabello con tullmas (aumentos coloridos en las trenzas).
        </p>
        <img src={huayo} alt='logo' className="caporales2-img"></img>

      </div>
</div>
  )
}
