import React from 'react'
import macheteros from '../assets/img/Macheteros.jpg'
import macheteros2 from '../assets/img/Macheteros2.jpg'

export const Beni = () => {
  return (
    <div className="container">
      <div className="column-leftbeni">
        {/* Contenido de la columna izquierda */}
        <h2>Macheteros</h2>
        <h4>Origen</h4>
        <p>
        Antes de la llegada de los espaoles su significado fue todo lo que muere resucita. La danza es una representacin de la resurreccin de Jesucristo y su ascensin a los cielos, desde la interpretacin nativa. El origen de Macheteros data desde la poca de la Colonia durante las misiones jesuticas.
        Tambin se cree que deriva del juego de esgrima llegado al pas y adaptado por campesinos de esta regin a manera de juego de machetes entre hombres y mujeres.
        En 2019 la danza de los Macheteros fue declarada Patrimonio Cultural Inmaterial de Bolivia mediante ley.
        </p>
        <h4>Vestimenta</h4>
        <p>
        Cada danzante lleva en la mano derecha un machete de madera , pero nada, ni la calculada cadencia de los pasos de baile pueden competir con lo belleza del tocado. Las plumas de parabas, de las que abundan en la selva amaznica, van artsticamente ordenadas de acuerdo con la intensidad de los colores. Sujeta del armazn embellecido por las piezas de colores, se extiende sobre la espalda del bailarn un cuero de piel de tigre que llega hasta los talones, de donde cuelgan cintas o cordones conputo.
        Esta danza puede ser muy peligrosa, pues los bailarines bailan con machetes haciendo el rol de pelear por una mujer.
        Tambin semillas de paichaches, unos granos que suenan como cascabel.
        </p>
      </div>
      <div className="column-rightbeni">
        {/* Contenido de la columna derecha */}
        
        <img src={macheteros} alt='logo' className="caporales2-img"></img>
        <br></br>
        <br></br>
        <img src={macheteros2} alt='logo' className="caporales2-img"></img>
    
      </div>
</div>
  )
}
