import React from 'react'
import cuecacochabambina from '../assets/img/Cueca cochabambina.jpg'
import cuecacochabambina2 from '../assets/img/cueca cochabambina (2).jpg'
import salay from '../assets/img/salay.jpg'
import salay2 from '../assets/img/salay (2).jpg'

export const Cochabamba = () => {
  return (
    <div className="container">
      <div className="column-leftcochabamba">
        {/* Contenido de la columna izquierda */}
        <h2>Cueca Cochabambina</h2>
        <h4>Origen </h4>
        <p>
        La danza cueca cochabambina es una danza popular de Cochabamba, es un juego de seduccin entre un hombre y una mujer. La mano derecha en la cintura y con la izquierda bate el pauelo acosando a la dama, mientras ella juega a la indiferencia siguiendo coqueta el baile de su pareja.
        En general esta danza es muy popular en toda Bolivia, tiene como ancestro al zapateo espaol, en la poca de la colonia estuvo reservada para la clase aristocrtica de dicho periodo y tambin de los primeros aos de la independencia boliviana.
        El origen de la cueca boliviana se remonta a la poca colonial, siendo resultado de la mezcla de lo indgena y lo espaol. Durante la Guerra del Chaco (1932-1935), un momento histrico importante para la consolidacin de la identidad criollo-mestiza, la cueca se transform en un discurso patritico que aviv y exalt la moral de los soldados y de la sociedad boliviana. Desde entonces, la letra de la cueca boliviana es potica y profunda, evoca pocas histricas, historias de amor y desamor y sentimientos nacionales.
        </p>
        <img src={cuecacochabambina} alt='logo' className="caporales2-img"></img>
        <h4>Vestimenta</h4>
        <p>
        Femenino consiste en una pollera ligera hasta las. rodillas y un alto sombrero blanco, y la vestimenta. masculina se caracteriza por llevar un chaleco y. sombrero negro.
        </p>
        <img src={cuecacochabambina2} alt='logo' className="caporales2-img"></img>

        <br></br>
        <br></br>

      
      </div>
      <div className="column-rightcochabamba">
        {/* Contenido de la columna derecha */}

        <h2>Salay</h2>
        <h4>Origen</h4>
        <p>
        La danza Salay tambin llamado Salaque se baila al son de huaynos, al ritmo de los charangos hombres y mujeres van intercalando versos, generalmente cantadas en quechua.
        Es una danza de galanteo en la cual el varn, a travs del baile se ingenia para crear movimientos graciosos y zapateo, buscando ganar la atraccin de su pareja, la cual es finalmente atrada y convencida por los encantos dispensados, termina en sus brazos.
        El nombre Salay deriva de la palabra que, en muchas ocasiones, se repite a modo de letana en los huaynos que acompaan al baile.
        El origen de la danza Salay viene de los valles de Potos, Chuquisaca y Cochabamba. Segn Uvaldo Romero, se origina de danzas tpicas ancestrales con races de las celebraciones del cultivo y agricultura. Sus pasos imitan los de un agricultor cosechando y sembrando semillas.
        La danza Salay, naci en el Barrio de Jaihuayco, donde se caracteriz los pasos, el estilo y coqueteo de la danza.
        El Salay lleg a la ciudad a travs una fraternidad cochabambina y en 1990 surgen ya varias otras fraternidades que empiezan a bailar esta danza y le dan un toque urbano. Por lo tanto, la danza se considera hoy en da una danza urbana con caractersticas rurales.
        </p>
        <img src={salay} alt='logo' className="caporales2-img"></img>
        <h4>Vestimenta</h4>
        <p>
        Varones
        Sombrero
        Camisa de manga larga
        Chaleco
        Pantaln con pinzas en la cintura
        Faja (chumpi)
        Zapatos de cuero
        Mujeres
        Sombrero
        Camisa con los mismos detalles de la pollera
        Faja de colores con aguayo
        Pollera (cinco centmetros arriba de la rodilla)
        Fuste pegado al cuerpo
        Zapatos de taco medio
        Ambos trajes se adornan con cintas de colores conjuntos que adornan el traje y simbolizan el calendario de Santa Vera Cruz y Todos Santos.
        </p>
        <img src={salay2} alt='logo' className="caporales2-img"></img>

        <br></br>
        <br></br>
      </div>
</div>
  )
}
